import React from 'react';
import {
  Row,
  Col,
  Card,
  Form,
  Input,
  Button,
  message,
  Popconfirm,
  Menu,
  Dropdown,
  Icon,
  Select,
  DatePicker,
  Badge,
} from 'antd';
import { injectIntl, FormattedMessage, defineMessages } from 'react-intl';

import StandardTable from '../../../../components/StandardTable';
import PhotoBox from '../../../../components/PhotoBox'
import ImageViewer from '../../../../components/ImageViewer'
import ListHeader from '../../../../components/ListHeader'
import SelectAccount from '../../../../components/SelectAccount';


import {
  getVisitorList,
  deleteVisitors,
} from '../../../../api/personnel';


import { defaultPageCount, fixUndefinedData, parseDate, dateTimeToMoment,getDow, get_date} from '../../../../utils/utils';
import { cancelApiRequsets } from '../../../../utils/fetch';
import { loadState } from '../../../../utils/localStorage';
import maxLength from '../../../../utils/maxLength'

import styles from '../index.module.scss';

const FormItem = Form.Item;
const Option = Select.Option;
const { RangePicker } = DatePicker;

const messages = defineMessages({
  personnelName: {
    id: 'personnel.name',
    defaultMessage: '',
  },
  photo: {
    id: 'personnel.photo',
    defaultMessage: '',
  },
  mobilePhone: {
    id: 'personnel.mobilePhone',
    defaultMessage: '',
  },
  visitReason: {
    id: 'personnel.visitReason',
    defaultMessage: ''
  },
  visitTimeRange: {
    id: 'personnel.visitTimeRange',
    defaultMessage: ''
  },
  identifier: {
    id: 'common.identifier',
    defaultMessage: ''
  },
  sourceAccount: {
    id: 'account.sourceAccount',
    defaultMessage: ''
  },
  inviter: {
    id: 'personnel.inviter',
    defaultMessage: ''
  },
  inviteTime: {
    id: 'personnel.inviteTime',
    defaultMessage: ''
  },
  status: {
    id: 'common.status',
    defaultMessage: '',
  },
  accepted: {
    id:'common.accepted',
    defaultMessage: ''
  },
  confirmPending: {
    id: 'common.confirmPending',
    defaultMessage: ''
  },
  rejected: {
    id: 'common.rejected',
    defaultMessage: ''
  },
  search: {
    id: 'common.search',
    defaultMessage: '',
  },
  reset: {
    id: 'common.reset',
    defaultMessage: ''
  },
  all: {
    id: 'common.all',
    defaultMessage: ''
  },
  failedToGetData: {
    id: 'common.error.failedToGetData',
    defaultMessage: '!'
  },
  batchDelete: {
    id: 'common.batchDelete',
    defaultMessage: '',
  },
  batchDeleteConfirm: {
    id: 'common.batchDelete.confirm',
    defaultMessage: '?'
  },
  batchDeleteSuccess: {
    id: 'common.batchDelete.success',
    defaultMessage: '!'
  },
  batchDeleteError: {
    id: 'common.batchDelete.error',
    defaultMessage: '!'
  },
  visitorList: {
    id: 'personnel.visitorList',
    defaultMessage: '',
  },
  confirmDelete: {
    id: 'common.confirmDelete',
    defaultMessage: ''
  },
  cancel: {
    id: 'common.cancel',
    defaultMessage: ''
  },
})
class InvitedVisitor extends React.Component {
  columns = [
    {
      title: <FormattedMessage {...messages.personnelName}/>,
      dataIndex: 'people_name',
      render: (text, record) => (
        <React.Fragment>
          <span className={styles.wordBreak}>{text}</span>
        </React.Fragment>
      )
    },
    {
      title: <FormattedMessage {...messages.photo}/>,
      dataIndex: 'image',
      render: (text, record) =>
        <React.Fragment>
          {record.image && <PhotoBox photo={record.thumbnail} width={50} height={50} onClick={() => {this.showImage(text)}}/>}
        </React.Fragment>
    },
    {
      title: <FormattedMessage {...messages.mobilePhone}/>,
      dataIndex: 'telephone'
    },
    {
      title: <FormattedMessage {...messages.visitReason}/>,
      dataIndex: 'reasons_for_access'
    },
    {
      title: <FormattedMessage {...messages.visitTimeRange}/>,
      dataIndex: 'visit_time',
      render: (text, record) =>
        <React.Fragment>
          {parseDate(record.start_time)} - {parseDate(record.end_time)}
        </React.Fragment>
    },
    {
      title: <FormattedMessage {...messages.sourceAccount}/>,
      dataIndex: 'account_name',
      render: (text, record) => (
        <React.Fragment>
          <span className={styles.wordBreak}>{text}</span>
        </React.Fragment>
      )
    },
    {
      title: <FormattedMessage {...messages.inviter}/>,
      dataIndex: 'inviter_name',
      render: (text, record) => (
        <React.Fragment>
          <span className={styles.wordBreak}>{text}</span>
        </React.Fragment>
      )
    },
    {
      title: <FormattedMessage {...messages.inviteTime}/>,
      dataIndex: 'inviter_time',
      render: (text, record) => (
        <React.Fragment>
         {parseDate(record.inviter_time)}
        </React.Fragment>
      )
    },
    {
      title: <FormattedMessage {...messages.status}/>,
      dataIndex: 'inviter_status',
      render: (text, record) =>
      <React.Fragment>
        <Badge status={['success','processing','error'][text - 1]} />
        {[
          <FormattedMessage {...messages.accepted}/>,
          <FormattedMessage {...messages.confirmPending}/>,
          <FormattedMessage {...messages.rejected}/>
        ][text-1]}
      </React.Fragment>
    },
  ];

  state = {
    selectedRows: [],
    loading: false,
    confirmLoading: false,
    locked: false,
    showView: false,
    dataList: [],
    // 
    isShowImage: false,
    // url
    imageViewUrl: '',
    // 
    search_form: {},
    total: 0,
    page_number: 1,
    page_size: defaultPageCount,
    batchDeleteMode: false,
  };

  componentDidMount() {
    const initAccountIds = this.getInitAccountIds();
    const form = this.props.form;
    const accountId =initAccountIds.slice(-1)[0]
    this.setState({
      search_form: {
        account_ids: initAccountIds,
        account_id: accountId,
      }
    });
    form.setFieldsValue({
      account_ids: initAccountIds,
      account_id: accountId,
    }, () => {
      this._loadAsyncData();
    })
  }

  onAccountChange = (value) => {
    const form = this.props.form
    form.setFieldsValue({
      account_id: value[value.length - 1]
    })
  }

  getInitAccountIds = () => {
    const userInfo = loadState('userInfo', true);
    const role = userInfo.role;
    const account_id = userInfo.account_id;
    let res = [account_id];
    // admin,accountId,admin/
    if (role === 1 && this.accountId) {
      if (this.parentId) {
        res.push(Number(this.parentId))
      }
      res.push(this.accountId)
    }
    return res
  }




  componentWillUnmount() {
    cancelApiRequsets();
  }

  _loadAsyncData() {
    this.setState({
      loading: true
    });

    let { page_number, page_size, search_form = {} } = this.state;
    let dataList = [];

    getVisitorList({ page_number, page_size, ...search_form })
      .then(response => {
        let data = response.data;
        if (response.code === 1000) {
          dataList = data.peoples || [];
          let total = data.total_number || 0;
          this.setState({
            dataList,
            total,
            selectedRows: []
          });
        } else {
          this.setState({
            dataList: [],
            selectedRows: []
          });
          message.error(response.msg || this.props.intl.formatMessage(messages.failedToGetData));
        }
      })
      .catch(e => {
        console.log(e);
      })
      .finally(() => {
        this.setState({
          loading: false
        });
      });
  }

  toggleBatchDelete = (isDeleteMode) => {
    // 
    if (!isDeleteMode) {
      // 
      this.setState({
        selectedRows: []
      })
    }
    this.setState({
      batchDeleteMode: !this.state.batchDeleteMode
    })
  }

  /**
   * 
   */
  onDeleteAll = () => {
    if (this.state.selectedRows) {
      const ids = this.state.selectedRows.reduce((res, item) => {
        res.push(item.people_id)
        return res
      }, [])
      deleteVisitors(ids).then((res) => {
        if (res.code === 1000) {
          message.success(this.props.intl.formatMessage(messages.batchDeleteSuccess));
          this.setState({
            selectedRows: []
          })
          this._loadAsyncData();
        } else {
          message.error(res.msg || this.props.intl.formatMessage(messages.batchDeleteError));
        }
      })
      .catch(error => {
        // handle error
        message.error(this.props.intl.formatMessage(messages.batchDeleteError));
      })
    }
  }

  /**
   * 
   */
  handleStandardTableChange = (pagination, filtersArg, sorter) => {
    let { page_number, page_size } = this.state;

    if (page_size !== pagination.pageSize) {
      // page size changed, then reset page number to 1
      page_number = 1;
    } else {
      page_number = pagination.current;
    }

    page_size = pagination.pageSize;

    this.setState(
      {
        page_number,
        page_size
      },
      this._loadAsyncData
    );
  };

  /**
   * 
   */
  handleFormReset = () => {
    const { form } = this.props;
    form.resetFields();
    this.setState(
      {
        page_number: 1,
        search_form: {}
      },
      this._loadAsyncData
    );
  };

  /**
   * 
   * @param {Object} e 
   */
  handleSearch = e => {
    e.preventDefault();

    const { form } = this.props;

    form.validateFields((err, fieldsValue) => {
      if (err) return;
      const date = fieldsValue.date || ['', '']
      const begin = get_date((date[0] || '').valueOf())
      const end = get_date((date[1] || '').valueOf())
      const searchData = {
        ...fieldsValue,
        begin: begin ?  begin + ' 00:00:00' : '',
        end: end ? end + ' 23:59:59': '',
      }
      this.setState(
        {
          page_number: 1,
          search_form: searchData
        },
        this._loadAsyncData
      );
    });
  };


  /**
   * 
   * @param {String} url Url
   */
  showImage = (url) => {
    this.setState({
      isShowImage: true,
      imageViewUrl: url,
    })
  }

  /**
   * 
   */
  closeImage = () => {
    this.setState({
      isShowImage: false,
      imageViewUrl: '',
    })
  }


  /**
   * 
   * @param {Array[Object]} 
   */
  handleSelectRows = rows => {
    this.setState({
      selectedRows: rows
    });
  };

  renderSimpleForm() {
    const {
      form: { getFieldDecorator }
    } = this.props;

    return (
      <Form onSubmit={this.handleSearch} layout="inline">
        <Row gutter={{ md: 8, lg: 24, xl: 48 }}>
          {(loadState('userInfo', true) || {}).role !== 3 && <Col md={8} sm={24}>
          {getFieldDecorator('account_id')(<Input type="hidden"/>)}
          <FormItem label={<FormattedMessage {...messages.sourceAccount}/>}>
          {getFieldDecorator('account_ids')(
              <SelectAccount onChange={this.onAccountChange}/>
            )}

          </FormItem>
          </Col>}
          <Col md={8} sm={24}>
            <FormItem label={<FormattedMessage {...messages.personnelName}/>}>
              {getFieldDecorator('people_name')(
                <Input  maxLength={maxLength.userName}/>
              )}
            </FormItem>
          </Col>
          <Col md={8} sm={24}>
            <FormItem label={<FormattedMessage {...messages.inviter}/>}>
              {getFieldDecorator('inviter_name')(
                <Input  maxLength={maxLength.userName}/>
              )}
            </FormItem>
          </Col>
          <Col md={8} sm={24}>
            <FormItem label={<FormattedMessage {...messages.inviteTime}/>}>
            {getFieldDecorator('date')(
                <RangePicker
                  style={{ width: '100%' }}
                  format="YYYY-MM-DD"
                  disabledDate={current => { return current && current.isAfter && current.isAfter(getDow())} }/>
              )}
            </FormItem>
          </Col>
          <Col md={8} sm={24}>
            <span className={styles.submitButtons}>
              <Button type="primary" htmlType="submit">
              <FormattedMessage {...messages.search}/>
              </Button>
              <Button style={{ marginLeft: 8 }} onClick={this.handleFormReset}>
              <FormattedMessage {...messages.reset}/>
              </Button>
            </span>
          </Col>
        </Row>
      </Form>
    );
  }

  render() {
    const {
      loading,
      selectedRows,
      dataList,
      total,
      page_number,
      page_size,
    } = this.state;

    const pagination = {
      current: page_number,
      pageSize: page_size,
      total,
    };

    return (
      <React.Fragment>

        <Card bordered={false}>
          <div className={styles.tableList}>
            <div className={styles.tableListForm}>
              {this.renderSimpleForm()}
            </div>

            <ListHeader
              title={<FormattedMessage {...messages.visitorList}/>}
              customButton={
                <React.Fragment>
                  {this.state.batchDeleteMode ? <React.Fragment>
                    <Button onClick={() => this.toggleBatchDelete(false)} style={{marginRight: 5}}>
                      <FormattedMessage {...messages.cancel}/>
                    </Button>
                    {selectedRows.length <= 0 ? 
                     <Button disabled><FormattedMessage {...messages.confirmDelete}/></Button>:
                     <Popconfirm
                        title={<FormattedMessage {...messages.batchDeleteConfirm}/>}
                        onConfirm={() => this.onDeleteAll()}>
                         <Button><FormattedMessage {...messages.confirmDelete}/></Button>
                      </Popconfirm>
                    }
                  </React.Fragment> : <React.Fragment>
                    <Button onClick={() =>this.toggleBatchDelete(true)} >
                      <FormattedMessage {...messages.batchDelete}/>
                    </Button>
                  </React.Fragment>}
                </React.Fragment>
              }
              style={{ marginBottom: 5 }}
          />

            <StandardTable
              rowKey="people_id"
              selectedRows={selectedRows}
              selectable={this.state.batchDeleteMode}
              pagination={pagination}
              loading={loading}
              showAlert={false}
              dataList={dataList}
              columns={this.columns}
              onSelectRow={this.handleSelectRows}
              onChange={this.handleStandardTableChange}
            />
          </div>
        </Card>


        <ImageViewer imageUrl={this.state.imageViewUrl} isOpen={this.state.isShowImage} onClose={this.closeImage}></ImageViewer>


      </React.Fragment>
    );
  }
}

export default injectIntl(Form.create()(InvitedVisitor));
