import React from 'react';
import styles from './index.module.scss';

const loadImg = (event, objectFit, width, height) => {
  let target = event.target;
  let containerAspectRatio = width / height;
  let imgAspectRatio = target.naturalWidth / target.naturalHeight;

  if (imgAspectRatio > containerAspectRatio) {
    if (objectFit === 'cover') {
      target.style.height = '100%';
      target.style.width = 'auto';
    } else {
      // contain
      target.style.width = '100%';
      target.style.height = 'auto';
    }
  } else {
    if (objectFit === 'cover') {
      target.style.width = '100%';
      target.style.height = 'auto';
    } else {
      // contain
      target.style.height = '100%';
      target.style.width = 'auto';
    }
  }
};

export default ({
  clsName = '',
  photo,
  width = 60,
  height = 80,
  objectFit = 'cover',
  ...rest
}) => (
  <div
    className={`${clsName} ${styles.container}`}
    style={{ width, height }}
    {...rest}
  >
    <img
      src={photo}
      alt=""
      className={styles.img}
      onLoad={e => loadImg(e, objectFit, width, height)}
    />
  </div>
);
