import React, {Component} from 'react'
import {Tabs, Card} from 'antd'
import { injectIntl, FormattedMessage, defineMessages } from 'react-intl';

import InvitedlVisitor from './Invited/index'
import TemporyVisitor from './Temporary/index'

import {getUUID} from '../../../utils/utils'

const TabPane = Tabs.TabPane;

const messages = defineMessages({
  personnel: {
    id: 'personnel.personnel',
    defaultMessage: ''
  },
  visitor: {
    id: 'personnel.visitor',
    defaultMessage: ''
  },
  temporaryVisitor: {
    id: 'personnel.temporaryVisitor',
    defaultMessage: ''
  },
  invitedVisitor: {
    id: 'personnel.invitedVisitor',
    defaultMessage: ''
  },
})

class Record extends Component {
  state = {
    invitedKey: getUUID(),
    temporaryKey: getUUID(),
  }
  onTabChange = (key) => {
    let curr = ['invitedKey','temporaryKey'][key-1]
    this.setState({
      [curr]: getUUID()
    })
  }
  render() {
    return (
      <Card bordered={false}>
        <Tabs defaultActiveKey="1" onChange={this.onTabChange} animated={false}>
          <TabPane tab={<FormattedMessage {...messages.invitedVisitor}/>} key="1">
            <InvitedlVisitor key={this.state.invitedKey}/>
          </TabPane>
          {/* <TabPane tab={<FormattedMessage {...messages.temporaryVisitor}/>} key="2">
            <TemporyVisitor  key={this.state.temporaryKey}/>
          </TabPane> */}
        </Tabs>
      </Card>
    )
  }
}

export default injectIntl(Record)