import fetch from '../utils/fetch'

const apis = {
  people: '/api/people',
  batchAdd: '/api/people/batch',
  list: '/api/people/list',
  approval: '/api/people/approval',
  approvalVisitor: '/api/people/approval/visitors',
  reset: '/api/people/reset',
  visitors: '/api/people/visitors',
}


/**
 *
 *
 * @export
 * @param {Object} data
 * @returns
 */
export function getPeoples(data) {
  return fetch.get(apis.list, { params: data });
}

/**
 *,formData
 *
 * @export
 * @param {Object} model
 * @returns
 */
export function addPeople(model) {
  var formData = new FormData();
  formData = commonFormData(formData, model);
  return fetch.post(apis.people, formData);
}

/**
 *,formData
 *
 * @export
 * @param {Object} model
 * @returns
 */
export function updatePeople(model) {
  var formData = new FormData();
  formData = commonFormData(formData, model);

  return fetch.put(apis.people, formData);
}

/**
 *
 *
 * @export
 * @param {Array} data
 * @returns
 */
export function deletePeople(id) {
  return fetch.delete(apis.people, {
    data: {
      people_id: id
    }
  });
}

export function batchDeletePeople(ids) {
  return fetch.delete(apis.list, {
    data: {
      people_ids: ids
    }
  })
}

/**
 *
 *
 * @export
 * @param {Number} id
 * @returns
 */
export function getPeopleInfo(id) {
  return fetch.get(apis.people, {
    params: {
      people_id: id,
    }
  })
}

/**
 * 
 *
 * @export
 * @param {*} formData
 * @returns
 */
export function batchAddPeople(formData) {
  return fetch.post(apis.batchAdd, formData)
}

/**
 * 
 * @param {*} data 
 */
export function approval(data) {
  return fetch.post(apis.approval, data)
}

/**
 * 
 * @param {*} data 
 */
export function approvalVisitor(data) {
  return fetch.post(apis.approvalVisitor, data)
}

/**
 * 
 * @param {*} data 
 */
export function resetPwd(data) {
  return fetch.post(apis.reset, data)
}

// 
export function getVisitorList(data) {
  return fetch.get(apis.visitors, { params: data });
}

// 
export function deleteVisitors(ids) {
  return fetch.delete(apis.visitors, {
    data: {
      people_ids: ids
    }
  });
}

/**
 *formData
 *
 * @param {FormData} formData
 * @param {Object} model
 * @returns {FormData} formData
 */
function commonFormData(formData, model) {
  const keys = Object.keys(model)
  keys.forEach((it) => {
    formData.append(it, model[it])
  })
  return formData;
}

